import logo from './logo.svg';
import keyIcon from './icons/key.svg';
import mergeIcon from './icons/crafting.svg';
import React  from 'react';
import './App.css';

/*
2 Modes:
1.
*/

class Top extends React.Component {
  render(){
    return (
      <div className="header-back">
      <p className="header-text">LCP Unlocks</p>
      </div>
    );
  }
}

class SelectModeButton extends React.Component {
  render(){
    return (<div className="selectable-inset" onClick={this.props.onClick}>
      <img src={this.props.iconInfo.icon} className={this.props.iconInfo.iconClass} fill="#FF00FF"/>
      <p className="select-button-label">{this.props.label}</p>
    </div>);
  }
}

class ActivePage extends React.Component {
  render(){
    return (<div>
      {this.props.children}
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props){
    super(props);
    this.state={mode:"selecting"};
  }


  render(){
    var filling = null;
    switch (this.state.mode) {
      case "unlocking":
        filling = (<ActivePage>

          </ActivePage>);
        break;
      case "augmenting":
        filling = (<ActivePage>

          </ActivePage>);
      break;
      case "selecting":/*One button per state.*/
      default:
        filling = (<div className="ModeButtons">
            <SelectModeButton iconInfo={{icon:keyIcon,   iconClass:"key-logo"}} label="Unlock Content" onClick={() => this.setState({mode:"unlocking"})}/>
            <SelectModeButton iconInfo={{icon:mergeIcon, iconClass:"craft-logo"}} label="Patch LCP" onClick={() => this.setState({mode:"augmenting"})}/>
          </div>);
    };

    return (<div className="App">
      <header><Top/></header>
      {filling}
      <footer><p>"Creative Commons 10th Birthday Celebration San Francisco" by tvol is licensed under CC BY 2.0</p></footer>
    </div>
    );

    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
          </a>
        </header>
      </div>
    );
  }
}

export default App;
